n      -webkit-box-orient: vertical;\n    }\n  }\n}\n\n.restaurant-item__header img {\n  width: 100%;\n  height: 200px;\n  object-fit: cover;\n  object-position: center;\n}\n \n\n/*\n  Restaurant\n*/\n\n.restaurant {\n  margin: 0 auto;\n  width: 100%;\n  max-width: 800px; \n\n  h2 {\n    font-size: 28px;\n  }\n\n  &__title {\n    margin-bottom: 30px;\n    &__categorie {\n      display: flex;\n      margin-bottom: 10px;\n      h2 {\n        margin-right: 10px;\n      }\n      p {\n        margin-top: 13px;\n        margin-right: 5px;\n        font-size: 15px;\n        color: #D0C9C0;\n        text-align: center;\n      }\n    }\n\n    &__categorie_ {\n      display: flex;\n      margin-bottom: 10px;\n    }\n\n    &__addres {\n      font-size: 14px;\n\n      span {\n        font-size: 20px;\n        margin-right: 5px;\n      }\n    }\n\n  }\n\n  &__poster {\n    margin-bottom: 100px;\n\n    img {\n      width: 100%;\n      height: 350px;\n      object-fit: cover;\n      object-position: center;\n    }\n  }\n\n  &__description {\n    margin-bottom: 100px;\n    text-align: center;\n    \n    h2 {\n      margin-bottom: 30px;\n    }\n\n    p {\n      font-size: 14px;\n    }\n  }\n\n  &__menus {\n    margin-bottom: 100px;\n    text-align: center;\n\n    h2 {\n      margin-bottom: 30px;\n    }\n\n\n    &__list {\n      display: grid;\n      grid-template-columns: 1fr 1fr;\n      grid-column-gap: 30px;\n      grid-row-gap: 40px;\n\n      &__foods {\n        border: 1px solid;\n        padding: 15px;\n\n        &__title {\n          border-bottom: 1px solid;\n          margin-bottom: 15px;\n          font-size: 14px;\n          padding-bottom: 10px;\n        }\n\n        li {\n          list-style-type: none;\n          font-size: 14px;\n          margin-bottom: 15px;\n        }\n      }\n\n      &__drinks {\n        border: 1px solid;\n        padding: 15px;\n\n        &__title {\n          border-bottom: 1px solid;\n          margin-bottom: 15px;\n          font-size: 14px;