0%;\n  max-width: 800px;\n  top: 75px;\n  right: 10px;\n  display: flex;\n  justify-content: end;\n }\n\n.like {\n  min-width: 44px;\n  min-height: 44px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: rgba($color-text, 1);\n  border-radius: 50%;\n  border: 0;\n  font-size: 18px;\n  color: red;\n  cursor: pointer;\n}\n\n.likes {\n  min-width: 44px;\n  min-height: 44px;\n}\n\n\n/*  Loading  */\n\n.loader {\n  display: flex;\n  justify-content: center;\n\n  &__icon {\n    border: 16px solid #f3f3f3;\n    border-radius: 50%;\n    border-top: 16px solid #4E6C50;\n    margin-top: 20%;\n    margin-bottom: 20%;\n    width: 120px;\n    height: 120px;\n    -webkit-animation: spin 2s linear infinite; /* Safari */\n    animation: spin 2s linear infinite;\n  }\n}\n\n@-webkit-keyframes spin {\n  0% { -webkit-transform: rotate(0deg); }\n  100% { -webkit-transform: rotate(360deg); }\n}\n\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n/*\n  Error \n*/\n\n.error {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  flex-wrap: wrap;\n  padding: 20%;\n\n  &__text {\n    h1 {\n      font-size: 50px;\n    }\n    p {\n      font-size: 20px;\n    }\n  }\n}\n\n/*\n * footer\n */\n\nfooter {\n  width: 100%;\n  color: $color-text;\n  background-color: #395144;\n  padding: 2em;\n  text-align: center;\n  p {\n    font-size: .8em;\n    text-align: center;\n  }\n}"],"sourceRoot":""}]);
